@mixin smallDesktop {
  @media (max-width: 1263px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 923px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 575px) {
    @content;
  }
}

@mixin logo {
  padding: 0;
  margin: 0;
  line-height: 48px;
  font-size: 32px;
  color: white;
  font-weight: 600;

  @include mobile {
    line-height: 28px;
    font-size: 22px;
  }
}

.Text {
  @include logo;

  display: inline;
}

.Logo {
  @include logo;

  position: relative;
  cursor: pointer;
  text-decoration: none;

  &:active, &:hover, &:focus {
    text-decoration: underline;
  }

  &::after {
    content: '';
    width: 1px;
    height: 160%;
    position: absolute;
    right: -50px;
    top: -30%;
    background-color: rgba(255, 255, 255, 0.65);
  }

  & + .Text {
    margin-left: 96px;
  }
}

.Content {
  width: 100%;
  padding: 48px 16px;
  max-width: 1232px;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 24px;
}

.Icon {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  max-width: 100%;
}

.Header {
  position: relative;
  width: 100%;
  background: #38B4E7;

  @include smallDesktop {
    .Logo {
      &::after {
        width: auto;
        height: 1px;
        right: -16px;
        top: auto;
        left: -8px;
        bottom: -18px;
      }

      & + .Text {
        margin-left: 0;
        margin-top: 24px;
      }
    }

    .Text {
      display: block;
    }
  }

  @include tablet {
    text-align: center;

    .Content {
      padding-top: 16px;
      padding-bottom: 0;
    }

    .Logo {
      display: block;

      &::after {
        width: auto;
        height: 1px;
        right: 0;
        left: 0;
      }
    }

    .Icon {
      position: relative;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
